
import { getVideoType } from './util'
import flvjs from 'flv.js'
import Hls from 'hls.js'
type PlayerOptions = {
  isLiving?: boolean,
  url: string,
  autoplay?: boolean
}

class YMPlayer {
  private player: any
  private video: HTMLVideoElement = document.createElement('video')
  constructor(private container: HTMLElement, private playerOptions: PlayerOptions) {
    this.initPlayer()
  }
  initPlayer() {
    const type = getVideoType(this.playerOptions.url)
    if (type === 'flv' || 'mp4') {
      this.player = flvjs.createPlayer({
        type,
        url: this.playerOptions.url
      })
      this.player.attachMediaElement(this.video)
      this.player.load();
      if (this.playerOptions.autoplay) {
        this.video.play()
      }
    }
    if (type === 'm3u8') {
      this.player = new Hls();
      this.player.attachMedia(this.video);
      this.player.on(Hls.Events.MEDIA_ATTACHED, () => {
        this.player.loadSource(this.playerOptions.url);
        this.player.on(Hls.Events.MANIFEST_PARSED, (event: any, data: any) => {
          console.log(event, data)
          this.video.play()
        });
      });
    }
    if (!type) {
      this.video.src = this.playerOptions.url
      if (this.playerOptions.autoplay) {
        this.video.play()
      }
    }
    this.container.appendChild(this.video)

  }
  dispose() {

  }
}

export default YMPlayer